<script>
import { ref, computed } from 'vue';
import EditModal from './components/EditModal.vue'; 
import LogEditModal from './components/LogEditModal.vue'; 
import AddModal from './components/AddModal.vue'; 
import DeleteModal from './components/DeleteModal.vue'; 

const todo_data = ref([
  {
    id: 1,
    content: '牛乳を買う',
    status: true,
    limit: '2024-09-31',
    insertdate: '2024/08/31'
  },
  {
    id: 2,
    content: '砂糖を買う',
    status: true,
    limit: '2024-09-31',
    insertdate: '2024/08/31'
  },
  {
    id: 3,
    content: '卵を買う',
    status: true,
    limit: '2024-09-31',
    insertdate: '2024/08/31'
  }
]);

const allStatusTrue = computed(() => {
      return todo_data.value.every(todo => todo.status === true);
    });

const allStatusFlase = computed(() => {
    return todo_data.value.every(todo => todo.status === false);
  });

export default {
  components: {
    EditModal, 
    AddModal,
    DeleteModal,
    LogEditModal
  },
  data() {
    return {
      showEditModal: false,
      showAddModal: false,
      showDeleteModal: false,
      showLogEditModal: false,
      todos: todo_data,
      allStatusTrue: allStatusTrue,
      allStatusFlase: allStatusFlase,
      isOpen: false, // 折りたたみ状態を管理する
      selectedTodo: null // 選択されたTODOのデータを格納
    };
  },
  methods: {
    toggle() {
      this.isOpen = !this.isOpen; // 折りたたみ状態を切り替える
    },
    openEditModal(todo) {
      this.selectedTodo = todo; // 選択されたTODOを設定
    },
    isOverdue(limitDate) {
      const today = new Date().toISOString().split('T')[0]; // 今日の日付をYYYY-MM-DD形式で取得
      return limitDate < today;
    }
  }
};
</script>


<template>
<html>
  <h1>TODO List</h1>
  <h2>UNDONE</h2>
    <table>
      <thead>
        <tr>
          <th>完了</th>
          <th>期限</th>
          <th>TODO内容</th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <p v-if="allStatusTrue">
        現在のTODOはありません。<br>
        登録する場合は下の登録ボタンを押してください。<br>
        ↓<br>
      </p>
      <tbody v-else>
        <tr v-for="todo in todos" :key="todo.id">
          <template v-if="!todo.status">
            <th><input type="checkbox"></th>
            <th>{{ todo.limit }}</th>
            <th>{{ todo.content }}</th>
            <th> 
              <!-- 編集モーダルを開くボタン -->
              <button  @click="openEditModal(todo)">編集</button>             
            </th>
            <th> 
              <!-- 削除ダイアログを開くボタン -->
              <button  type="button" @click="showDeleteModal = true">削除</button> 
            </th>
          </template>
        </tr>
      </tbody>
    </table>
      <!-- 登録モーダルを開くボタン --> 
    <button  type="button" @click="showAddModal = true">登録</button>
    
    <EditModal v-if="selectedTodo" :todo="selectedTodo" @close="selectedTodo = null"></EditModal>
    <DeleteModal v-if="showDeleteModal" @close="showDeleteModal = false"></DeleteModal>
    <AddModal v-if="showAddModal" @close="showAddModal = false"></AddModal>
    

  <!-- 履歴のアコーディオンメニュー -->
    <div @click="toggle()" class="list-header">
      <h2>DONE</h2>
      <span>{{ isOpen ? '▲' : '▼' }}</span> <!-- 折りたたみ状態を表示 -->
    </div>
    <div v-if="isOpen" class="list-content">
      <div v-if="allStatusFlase">
        現在の完了済みTODOはありません。<br>
      </div>
      <div v-else>
        <tr v-for="todo in todos" :key="todo.id">
          <template v-if="todo.status">
            <th><input type="checkbox"></th>
            <th>{{ todo.limit }}</th>
            <th>{{ todo.content }}</th>
            <th> 
              <!-- 編集モーダルを開くボタン -->
              <button  type="button" @click="showLogEditModal = true">編集</button> 
              <LogEditModal v-if="showLogEditModal" @close="showLogEditModal = false"></LogEditModal>
            </th>
            <th> 
              <!-- 削除ダイアログを開くボタン -->
              <button  type="button" @click="showDeleteModal = true">削除</button> 
              <DeleteModal v-if="showDeleteModal" @close="showDeleteModal = false"></DeleteModal>
            </th>
          </template>
        </tr>
      </div>
    </div>
  </html>
</template>

<style>
.list-header {
  background-color: #f0f0f0;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list-content {
  background-color: #e0e0e0;
  padding: 10px;
  margin-top: 5px;
  border-radius: 5px;
}

.overdue {
  color: red;
  font-weight: bold;
}
</style>
